



















































/**
 * 회원 정보(프로필) 보기 또는 수정 페이지
 *
 * 서버에서 새로운 정보를 가져와서 보여준다.
 * - 최신 정보를 보여주기도 하고,
 * - 사용자 정보를 FORM 데이터 컨테이너로 그대로 사용하기 때문.
 *
 */
import Vue from "vue";
import Component from "vue-class-component";
import { ApiService } from "@/x-vue/services/api.service";
import { UserModel } from "@/x-vue/interfaces/interfaces";
import { AppService } from "@/service/app.service";
import UserAvatar from "@/x-vue/components/user/UserAvatar.vue";

@Component({
  components: {
    UserAvatar,
  },
})
export default class Profile extends Vue {
  app = AppService.instance;
  api: ApiService = ApiService.instance;

  loaded = false;

  user!: UserModel;

  async mounted(): Promise<void> {
    try {
      this.user = await this.api.otherUserProfile(this.$route.params.idx);
      console.log("this.user; ", this.user);
      this.loaded = true;
    } catch (e) {
      this.app.error(e);
    }
  }
}
